export default {
  props: {
    heading: {
      type: String,
      default: "Green Book"
    }
  },
  data() {
    return {
      posts: []
    };
  },
  mounted() {
    setTimeout(() => this.fetchPosts(), 2000)
    // this.fetchPosts();
  },
  methods: {
    async fetchPosts() {
      const INSTAGRAM_POSTS =
        "https://us-central1-nifty-memory-204406.cloudfunctions.net/server/instafeed/actions?username=bounipun&action=getPosts&limit=7";
      const postsResponse = await this.$axios.get(INSTAGRAM_POSTS, {
        withCredentials: false
      });

      if (postsResponse.data.payload) {
        // data.posts = [];
        let posts = [];
        postsResponse.data.payload.forEach((post, index) => {
          if (index > 5) return;

          if (post.media_type === "VIDEO") {
            posts.push({ imageLink: post.thumbnail_url, permalink: post.permalink });
            return;
          }

          posts.push({ imageLink: post.media_url, permalink: post.permalink });
        });
        this.posts = posts;
      }
    }
  }
};
